<template>
  <div class="app">
    <!--  -->
    <AppHeader/>
    <!--  -->
    <div class="app-body">
      <!--   -->
      <Sidebar :navItems="routes" />
      <!--   -->
      <main class="main">
        <!--  -->
        <breadcrumb :list="list" />
        <!--  -->
        <div class="container-fluid">
          <router-view></router-view>
        </div>
      </main>
      <!--   -->
      <AppAside/>
    </div>
    <!--  -->
    <AppFooter/>
  </div>
</template>

<script>
import {
  Header as AppHeader,
  Sidebar,
  Aside as AppAside,
  Footer as AppFooter,
  Breadcrumb,
} from '../components/base';

export default {
  name: 'full',
  components: {
    AppHeader,
    Sidebar,
    AppAside,
    AppFooter,
    Breadcrumb,
  },
  data() {
    return {};
  },
  computed: {
    name() {
      return this.$route.name;
    },
    list() {
      return this.$route.matched;
    },
    routes() {
      let list = this.$router.options.routes[0].children;
      let arr = [];
      list.forEach((n, i) => {
        if (!n.children) {
          n.meta.url = n.path;
          n.meta.name = n.meta.title;
          delete n.meta.title;
          arr.push(n.meta);
        }
      });
      return arr;
    },
  },
  mounted() {},
};
</script>



